export default{
  data() {
    return {
      selectData: '全部',
      dataType: '',
      tabList: [{
          name: '全部',
          dataType: 'all'
        }, {
          name: '待付款',
          dataType: 'payment'
        },{
          name: '待发货',
          dataType: 'delivery'
        }, {
          name: '待收货',
          dataType: 'received'
        }, {
          name: '待评价',
          dataType: 'comment'
        }
      ],
      scrollLeft: 0,
      // dataList: [{
      //   store: '苹果官方旗舰店',
      //   sum: '2',
      //   status: '已取消',
      //   price: 300,
      //   arr: [{
      //     imgUrl: '',
      //     title: 'Sony/索尼 MDR-ZX310头戴式监听重低耳耳',
      //     info: '黑色 官方标配'
      //   }]
      // }, {
      //   store: '香蕉官方旗舰店',
      //   sum: '2',
      //   status: '已完成',
      //   price: 200,
      //   arr: [{
      //     imgUrl: '',
      //     title: 'Sony/索尼 MDR-ZX310头戴式监听重低耳耳',
      //     info: '黑色 官方标配'
      //   }, {
      //     imgUrl: '',
      //     title: 'Sony/索尼 超级头盔',
      //     info: '黑色 官方标配'
      //   }]
      // }]
      dataList: []
    }
  },
  watch: {
    // 监听选中的订单类别，改变位置
    // selectData(val) {
    //   if (val === '已取消' || val === '已完成') {
    //     this.scrollLeft = 69
    //     return
    //   }
    //   if (val === '全部' || val === '待付款') {
    //     this.scrollLeft = 0
    //     return
    //   }
    // }
  },
  computed: {
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  onLoad(option) {
    this.selectData = option.name
    this.dataType = option.dataType
    console.log('分享文章详情页接受到的参数',this.selectData, this.dataType)
    this.getOrderInfo()
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    // 选择订单分类
    
    selectTab(item) {
      this.selectData = item.name
      this.dataType = item.dataType
      this.getOrderInfo()
    },
    goDetail(item) {
      uni.navigateTo({
        url: 'orderDetail?item=' + JSON.stringify(item)
      })
    },
    // 去商城
    goMarket() {
      uni.switchTab({
        url: '../market/market'
      })
    },
    // 获取订单数据
    getOrderInfo() {
      let that = this
      that.$http({
        url: that.$api.orderList,
        data: {
          dataType: that.dataType
        },
        cb: (err, res) => {
          if(!err && res.code === 1) {
            console.log('成功了加载订单', res.data.list.data)
            that.dataList = res.data.list.data
          } else if(res.code === 0) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '订单数据加载失败',
              icon: 'none'
            })
          }
        }
      })
    }
  }
}
